<template>
  <div id="two-step" class="hamburger-container" @click="toggleClick">
    <el-icon>
      <component :is="icon"></component>
    </el-icon>
  </div>
</template>

<script setup>
import {useStore} from "vuex";
import {computed} from "vue";

const store = useStore()
const toggleClick = () => {
  store.commit('changeSiderType')
}
const icon = computed(() => {
  return store.getters.siderType ? 'fold' : 'expand'
})
</script>

<style scoped>
.hamburger-container {
  margin-right: 16px;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  justify-content: center;
}
</style>
