<template>
  <el-breadcrumb separator=">">
    <el-breadcrumb-item v-for="(item,index) in breadcrumbList" :key="item.index">
      <!--      最后一项，不可点击-->
      <span v-if="index===breadcrumbList.length-1">{{ $t(`menus.${item.name}`) }}</span>
      <!--      不是最后一项，可点击-->
      <span v-else @click="handleRedirect(item.path)">{{ $t(`menus.${item.name}`) }}</span>
    </el-breadcrumb-item>
  </el-breadcrumb>
</template>

<script setup>
import {useRoute} from 'vue-router'
import {ref, watch} from "vue";
import {useRouter} from 'vue-router'

const router = useRouter()
const route = useRoute()
const breadcrumbList = ref([])
const initBreadcrumbList = () => {
  breadcrumbList.value = route.matched
}
watch(route, () => {
  initBreadcrumbList()
}, {
  deep: true,
  immediate: true
})
const handleRedirect = (path) => {
  router.push(path)
}
</script>


<style lang="scss" scoped>
.no-redirect {
  color: #97a8be;
  cursor: text;
}

.redirect {
  color: #666;
  font-weight: 600;
  cursor: pointer;

  &:hover {
    color: $menuBg;
  }
}
</style>
